        </el-form-item>
        <!--  -->
        <el-form-item style="margin-bottom:6px;" label="">
          <el-checkbox-group v-model="form.module">
            <el-checkbox
              style="width:218px;margin:0;padding: 0;"
              v-for="item in module"
              :key="item.label"
              :label="item.label">
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <!-- -->
        <el-form-item style="margin-bottom:6px;" label="">
          <el-checkbox-group v-model="form.areaLevel">
            <el-checkbox
              style="width:116px;margin:0;padding: 0;"
              v-for="item in areaLevel"
              :key="item.label"
              :label="item.label">
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <!-- -->
        <el-form-item style="margin-bottom:6px;" label="">
          <el-checkbox-group v-model="form.userLevel">
            <el-checkbox
              style="width:116px;margin:0;padding: 0;"
              v-for="item in userLevel"
              :key="item.label"
              :label="item.label">
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <!-- -->
        <el-form-item style="margin-bottom:6px;" label="">
          <el-checkbox-group v-model="form.userNewLevel">
            <el-checkbox
              style="width:116px;margin:0;padding: 0;"
              v-for="item in userNewLevel"
              :key="item.label"
              :label="item.label">
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <!-- -->
        <el-form-item style="margin-bottom:6px;" label="">
          <el-checkbox-group v-model="form.userFeeLevel">
            <el-checkbox
              style="width:116px;margin:0;padding: 0;"
              v-for="item in userFeeLevel"
              :key="item.label"
              :label="item.label">
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <!-- -->
        <el-form-item style="margin-bottom:6px;" label="">
          <el-checkbox-group v-model="form.itemFeeLevel">
            <el-checkbox
              style="width:116px;margin:0;padding: 0;"
              v-for="item in itemFeeLevel"
              :key="item.label"
              :label="item.label">
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <!-- -->
        <el-form-item style="margin-bottom:6px;" label="">
          <el-checkbox-group v-model="form.strategy">
            <el-checkbox
              style="width:116px;margin:0;padding: 0;"
              v-for="item in strategy"
              :key="item.label"
              :label="item.label">
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <!-- -->
        <el-form-item style="margin-bottom:6px;" label="/">
          <el-checkbox-group v-model="form.status">
            <el-checkbox
              style="width:116px;margin:0;padding: 0;"
              v-for="item in status"
              :key="item.label"
              :label="item.label">
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <!-- -->
        <el-form-item style="margin-bottom:6px;" label="">
          <el-checkbox-group v-model="form.sub">
            <el-checkbox
              style="width:116px;margin:0;padding: 0;"
              v-for="item in sub"
              :key="item.label"
              :label="item.label">
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <!-- -->
        <el-form-item style="margin-bottom:6px;" label="">
          <el-checkbox-group v-model="form.intime">
            <el-checkbox
              style="width:116px;margin:0;padding: 0;"
              v-for="item in intime"
              :key="item.label"
              :label="item.label">
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <!-- -->
        <el-form-item style="margin-bottom:6px;" label="/">
          <el-radio-group v-on:change="chose_num_or_rate" v-model="form.numRate">
            <el-radio :label="1"></el-radio>
            <el-radio :label="2"></el-radio>
          </el-radio-group>
        </el-form-item>
        <!-- -->
        <el-form-item v-if="formNumTargetType" style="margin-bottom:6px;" label="">
          <el-checkbox-group v-model="form.queryNum">
            <el-checkbox
              style="width:88px;margin:0;padding: 0;"
              v-for="item in queryNum"
              :key="item.label"
              :label="item.label">
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <!-- -->
        <el-form-item v-if="formRateTargetType" style="margin-bottom:6px;" label="">
          <el-checkbox-group v-model="form.queryRate">
            <el-checkbox
              style="width:88px;margin:0;padding: 0;"
              v-for="item in queryRate"
              :key="item.name"
              :label="item.label">
            </el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <!-- -->
        <el-form-item label="" style="width:670px;">
          <el-date-picker
            v-model="form.timeRange"
            type="daterange"
            align="right"
            unlink-panels
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            value-format="yyyy-MM-dd"
            :picker-options="time_range_option">
          </el-date-picker>
        </el-form-item>
        <!-- -->
        <el-form-item>
          <el-button type="primary" @click="on_submit"></el-button>
          <el-button @click="init"></el-button>
        </el-form-item>
