<script>
import axios from 'axios'
import RequestLineChart from '@/api/RequestLineChart'
import Highcharts from 'highcharts'
require('highcharts/modules/exporting')(Highcharts)
export default {
  name: 'MainExhibit',
  components: {
    RequestLineChart
  },
  methods: {
    /*  */
    chose_num_or_rate () {
      switch (this.form.numRate) {
        case 1 :
          this.formNumTargetType = true
          this.formRateTargetType = false
          this.form.queryRate = []
          break
        case 2 :
          this.formNumTargetType = false
          this.formRateTargetType = true
          this.form.queryNum = []
          break
      }
    },
    init () {
      this.form.app = ['']
      this.form.module = ['']
      this.form.areaLevel = ['']
      this.form.userLevel = ['']
      this.form.userNewLevel = ['']
      this.form.userFeeLevel = ['']
      this.form.itemFeeLevel = ['']
      this.form.strategy = ['']
      this.form.status = ['']
      this.form.sub = ['']
      this.form.intime = ['']
      this.form.queryRate = []
      this.form.timeRange = []
      this.form.queryNum = []
    },
    on_submit () {
      let j
      /*  */
      if (this.form.app.length <= 0) {
        this.$alert('app ', '', {confirmButtonText: ''})
      } else if (this.form.module.length <= 0) {
        this.$alert(' ', '', {confirmButtonText: ''})
      } else if (this.form.areaLevel.length <= 0) {
        this.$alert(' ', '', {confirmButtonText: ''})
      } else if (this.form.userLevel.length <= 0) {
        this.$alert(' ', '', {confirmButtonText: ''})
      } else if (this.form.userNewLevel.length <= 0) {
        this.$alert(' ', '', {confirmButtonText: ''})
      } else if (this.form.userFeeLevel.length <= 0) {
        this.$alert(' ', '', {confirmButtonText: ''})
      } else if (this.form.itemFeeLevel.length <= 0) {
        this.$alert(' ', '', {confirmButtonText: ''})
      } else if (this.form.strategy.length <= 0) {
        this.$alert(' ', '', {confirmButtonText: ''})
      } else if (this.form.status.length <= 0) {
        this.$alert(' ', '', {confirmButtonText: ''})
      } else if (this.form.sub.length <= 0) {
        this.$alert(' ', '', {confirmButtonText: ''})
      } else if (this.form.intime.length <= 0) {
        this.$alert(' ', '', {confirmButtonText: ''})
      } else if (this.form.queryNum.length <= 0 && this.form.queryRate.length <= 0) {
        this.$alert(' ', '', {confirmButtonText: ''})
      } else if (this.form.timeRange.length < 2) {
        this.$alert(' ', '', {confirmButtonText: ''})
      } else {
        let i
        const request = {}
        request['app'] = []
        for (i = 0; i < this.form.app.length; ++i) {
          for (j = 0; j < this.app.length; ++j) {
            if (this.app[j].label === this.form.app[i]) {
              request['app'].push(this.app[j].name)
              break
            }
          }
        }
        request['module'] = []
        for (i = 0; i < this.form.module.length; ++i) {
          for (j = 0; j < this.module.length; ++j) {
            if (this.module[j].label === this.form.module[i]) {
              request['module'].push(this.module[j].name)
              break
            }
          }
        }
        request['areaLevel'] = []
        for (i = 0; i < this.form.areaLevel.length; ++i) {
          for (j = 0; j < this.areaLevel.length; ++j) {
            if (this.areaLevel[j].label === this.form.areaLevel[i]) {
              request['areaLevel'].push(this.areaLevel[j].name)
              break
            }
          }
        }
        request['userLevel'] = []
        for (i = 0; i < this.form.userLevel.length; ++i) {
          for (j = 0; j < this.userLevel.length; ++j) {
            if (this.userLevel[j].label === this.form.userLevel[i]) {
              request['userLevel'].push(this.userLevel[j].name)
              break
            }
          }
        }
        request['userNewLevel'] = []
        for (i = 0; i < this.form.userNewLevel.length; ++i) {
          for (j = 0; j < this.userNewLevel.length; ++j) {
            if (this.userNewLevel[j].label === this.form.userNewLevel[i]) {
              request['userNewLevel'].push(this.userNewLevel[j].name)
              break
            }
          }
        }
        request['userFeeLevel'] = []
        for (i = 0; i < this.form.userFeeLevel.length; ++i) {
          for (j = 0; j < this.userFeeLevel.length; ++j) {
            if (this.userFeeLevel[j].label === this.form.userFeeLevel[i]) {
              request['userFeeLevel'].push(this.userFeeLevel[j].name)
              break
            }
          }
        }
        request['itemFeeLevel'] = []
        for (i = 0; i < this.form.itemFeeLevel.length; ++i) {
          for (j = 0; j < this.itemFeeLevel.length; ++j) {
            if (this.itemFeeLevel[j].label === this.form.itemFeeLevel[i]) {
              request['itemFeeLevel'].push(this.itemFeeLevel[j].name)
              break
            }
          }
        }
        request['strategy'] = []
        for (i = 0; i < this.form.strategy.length; ++i) {
          for (j = 0; j < this.strategy.length; ++j) {
            if (this.strategy[j].label === this.form.strategy[i]) {
              request['strategy'].push(this.strategy[j].name)
              break
            }
          }
        }
        request['status'] = []
        for (i = 0; i < this.form.status.length; ++i) {
          for (j = 0; j < this.status.length; ++j) {
            if (this.status[j].label === this.form.status[i]) {
              request['status'].push(this.status[j].name)
              break
            }
          }
        }
        request['sub'] = []
        for (i = 0; i < this.form.sub.length; ++i) {
          for (j = 0; j < this.sub.length; ++j) {
            if (this.sub[j].label === this.form.sub[i]) {
              request['sub'].push(this.sub[j].name)
              break
            }
          }
        }
        request['intime'] = []
        for (i = 0; i < this.form.intime.length; ++i) {
          for (j = 0; j < this.intime.length; ++j) {
            if (this.intime[j].label === this.form.intime[i]) {
              request['intime'].push(this.intime[j].name)
              break
            }
          }
        }
        request['target'] = []
        if (this.formNumTargetType) {
          for (i = 0; i < this.form.queryNum.length; ++i) {
            for (j = 0; j < this.queryNum.length; ++j) {
              if (this.queryNum[j].label === this.form.queryNum[i]) {
                request['target'].push(this.queryNum[j].name)
                break
              }
            }
          }
        } else {
          for (i = 0; i < this.form.queryRate.length; ++i) {
            for (j = 0; j < this.queryRate.length; ++j) {
              if (this.queryRate[j].label === this.form.queryRate[i]) {
                request['target'].push(this.queryRate[j].name)
                break
              }
            }
          }
        }
        request['timeRange'] = this.form.timeRange
        axios({
          url: 'http://10.26.24.87:32000/exhibit',
          method: 'post',
          data: request,
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
          }
        }).then(function (response) {
          const obj = response.data
          if ((response.status === 200) && (obj['Status'] === true)) {
            const lines = obj['Lines']
            const y = []
            for (let i = 0; i < lines.length; ++i) {
              const info = lines[i]
              const tmp = {}
              tmp['name'] = info['Introduction']
              tmp['data'] = info['Y']
              y.push(tmp)
            }
            Highcharts.chart('main-exhibit-show', {
              chart: {
                type: 'line'
              },
              credits: {
                enabled: false
              },
              title: {
                text: ''
              },
              xAxis: {
                categories: lines[0]['X']
              },
              yAxis: {
                title: {
                  text: '/(100%)'
                }
              },
              series: y,
              plotOptions: {
                line: {
                  cursor: 'pointer'
                },
                series: {
                  allowPointSelect: true
                }
              },
              exporting: {
                filename: ''
              },
              navigation: {
                buttonOptions: {
                  align: 'right'
                }
              }
            })
          } else {
            console.log('')
          }
        }).catch(function (error) {
          console.log(error)
        })
      }
    }
  },
  data () {
    return {
      /*  */
      time_range_option: {
        shortcuts: [{
          text: '',
          onClick (picker) {
            const end = new Date()
            end.setTime(end.getTime() - 3600 * 1000 * 24 * 2)
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 9)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '',
          onClick (picker) {
            const end = new Date()
            end.setTime(end.getTime() - 3600 * 1000 * 24 * 2)
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 32)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '',
          onClick (picker) {
            const end = new Date()
            end.setTime(end.getTime() - 3600 * 1000 * 24 * 2)
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 92)
            picker.$emit('pick', [start, end])
          }
        }],
        disabledDate (time) {
          const dt = new Date()
          dt.setUTCFullYear(2018, 10, 30)
          return time.getTime() < dt.getTime()
        }
      },
      form: {
        app: [''],
        module: [''],
        areaLevel: [''],
        userLevel: [''],
        userNewLevel: [''],
        userFeeLevel: [''],
        itemFeeLevel: [''],
        strategy: [''],
        status: [''],
        sub: [''],
        intime: [''],
        queryNum: [],
        queryRate: [],
        timeRange: [],
        numRate: 1
      },
      formNumTargetType: true,
      formRateTargetType: false,
      app: [
        {label: 'app', name: 'allApp'},
        {label: '', name: 'easouApp'},
        {label: '', name: 'weijuanApp'},
        {label: 'app', name: 'othApp'}
      ],
      module: [
        {label: '', name: 'allMdl'},
        {label: '', name: 'shfRecMdl'},
        {label: '-', name: 'shfGusMdl'},
        {label: '-', name: 'freRecMdl'},
        {label: '-', name: 'freGusMdl'},
        {label: '-', name: 'freByRecMdl'},
        {label: '', name: 'fre7TfMdl'},
        {label: '', name: 'monStmMdl'},
        {label: '-', name: 'foeCateRecMdl'},
        {label: '-', name: 'foeAutRecMdl'},
        {label: '-', name: 'foeRedRedMdl'},
        {label: '-', name: 'foeRedMorMdl'},
        {label: '-TOP', name: 'sehResTopMdl'},
        {label: '-', name: 'sehResGusMdl'},
        {label: '-', name: 'bakRedRedMdl'},
        {label: '-', name: 'chsGilStmMdl'},
        {label: '-', name: 'chsCmpMdl'},
        {label: '-', name: 'chsCmpStmMdl'},
        {label: '-', name: 'chsRakStmMdl'},
        {label: '-', name: 'chsRedRecMdl'},
        {label: '-', name: 'chsRedCatRecMdl'},
        {label: '-', name: 'chsStmMdl'},
        {label: '-', name: 'chsHotRecMdl'},
        {label: '-', name: 'chsBoyStmMdl'},
        {label: '-', name: 'chsChsRedMdl'},
        {label: '', name: 'extBlkMdl'}
      ],
      areaLevel: [
        {label: '', name: 'allArea'},
        {label: '', name: 'oneArea'},
        {label: '', name: 'twoArea'},
        {label: '', name: 'trdArea'},
        {label: '', name: 'fthArea'},
        {label: '', name: 'othArea'}
      ],
      userLevel: [
        {label: '', name: 'allUsrLevel'},
        {label: '', name: 'regUsrLevel'},
        {label: '', name: 'sphUsrLevel'},
        {label: '', name: 'othUsrLevel'}
      ],
      userNewLevel: [
        {label: '', name: 'allUsr'},
        {label: '', name: 'newUsr'},
        {label: '', name: 'oldUsr'},
        {label: '', name: 'othUsr'}
      ],
      userFeeLevel: [
        {label: '', name: 'allFeeUsr'},
        {label: '', name: 'freFeeUsr'},
        {label: '', name: 'mybChgFeeUsr'},
        {label: '', name: 'lgtChgFeeUsr'},
        {label: '', name: 'mdlChgFeeUsr'},
        {label: '', name: 'hghChgFeeUsr'},
        {label: '', name: 'othFeeUsr'}
      ],
      itemFeeLevel: [
        {label: '', name: 'allItmFee'},
        {label: '', name: 'chgItmFee'},
        {label: '', name: 'freItmFee'},
        {label: '', name: 'monItmFee'},
        {label: '', name: 'tfItmFee'},
        {label: '', name: 'allFreItmFee'},
        {label: '', name: 'othItmFee'}
      ],
      strategy: [
        {label: '', name: 'allRec'},
        {label: '', name: 'cat1Rec'},
        {label: '', name: 'cat2Rec'},
        {label: '', name: 'cotSimRec'},
        {label: '', name: 'popRec'},
        {label: '', name: 'simAthRec'},
        {label: '', name: 'catRec'},
        {label: '', name: 'livStmRec'},
        {label: '', name: 'icfKnnRec'},
        {label: '', name: 'ucfKnnRec'},
        {label: '', name: 'nerIcfKnnRec'},
        {label: '', name: 'codRec'},
        {label: '', name: 'subMdlRec'},
        {label: '', name: 'redMdlRec'},
        {label: '', name: 'othRec'}
      ],
      status: [
        {label: '', name: 'allStu'},
        {label: '', name: 'cmpStu'},
        {label: '', name: 'noCmpStu'},
        {label: '', name: 'othStu'}
      ],
      sub: [
        {label: '', name: 'allSub'},
        {label: '010', name: 'bt0to10Sub'},
        {label: '10100', name: 'bt10to1bSub'},
        {label: '1001', name: 'bt1bto1kSub'},
        {label: '11', name: 'bt1kto10kSub'},
        {label: '110', name: 'bt10kto100kSub'},
        {label: '101', name: 'bt100kto1000kSub'},
        {label: '11', name: 'bt1000kto10000kSub'},
        {label: '', name: 'othSub'}
      ],
      intime: [
        {label: '', name: 'allIn'},
        {label: '1', name: 'lesMonIn'},
        {label: '1~3', name: 'bt1mto3mIn'},
        {label: '3~12', name: 'bt3mto12mIn'},
        {label: '12~99', name: 'bt12mto99mIn'},
        {label: '', name: 'othIn'}
      ],
      queryNum: [
        {label: '', name: 'dspNum'},
        {label: '', name: 'clkNum'},
        {label: '', name: 'srbNum'},
        {label: '1', name: 'redNum1'},
        {label: '2', name: 'redNum2'}
      ],
      queryRate: [
        {label: '', name: 'clkDsp'},
        {label: '', name: 'subClk'},
        {label: '', name: 'subDsp'},
        {label: '1', name: 'redSub1'},
        {label: '1', name: 'redDsp1'},
        {label: '2', name: 'redSub2'},
        {label: '2', name: 'redDsp2'}
      ]
    }
  }
}
</script>

<style>
  #main-exhibit {
    width: 100%;
    padding: 6px 18px;
    background-color: #ffffff;
  }
  #main-exhibit>h2 {
    width: 180px;
    margin: auto;
    font-weight: lighter;
  }
  /*  */
  .main-exhibit-chose, .main-exhibit-chose > div{
    width: 100%;
    max-width: 2100px;
  }
  .main-exhibit-chose > div > small {
    font-size: 13pt;
  }
</style>



