<template>
  <el-menu id="i-main-menu" @select="selected_child" :collapse="isCollapse">
    <!-- 1 --  -->
    <el-menu-item index="1">
        <i class="el-icon-menu"></i>
        <span slot="title"></span>
    </el-menu-item>
    <!-- 1 --  -->
    <!-- -->
    <!-- 2 --  -->
    <el-submenu index="2">
      <template slot="title">
        <i class="el-icon-location"></i>
        <span></span>
      </template>
      <router-link to="/main_exhibit/"><el-menu-item index="2-1"></el-menu-item></router-link>
    </el-submenu>
    <!-- 2 --  -->
    <!-- 3 --  -->
    <el-submenu index="3">
      <template slot="title">
        <i class="el-icon-location"></i>
        <span></span>
      </template>
      <router-link to="/main_retention/"><el-menu-item index="3-1"></el-menu-item></router-link>
    </el-submenu>
    <!-- 2 --  -->
    <!-- -->
    <!-- 3 --  -->
    <el-submenu index="4">
      <template slot="title">
        <i class="el-icon-location"></i>
        <span></span>
      </template>
      <router-link to="/main_user_type/"><el-menu-item index="4-1"></el-menu-item></router-link>
    </el-submenu>
    <!-- 3 --  -->
    <!-- -->
  </el-menu>
</template>

<style>
  #i-main-menu {
    width: 66px;
    height: 100vh;
    top: 78px;
    left:0;
    z-index: 998;
    position: fixed;
  }
  #i-main-menu:not(.el-menu--collapse) {
    width: 260px;
    height: 100vh;
  }
</style>

<script>
export default {
  data: function () {
    return {
      isCollapse: true
    }
  },
  methods: {
    selected_child (index, indexPath) {
      if (index === '1') {
        //   
        this.isCollapse = !(this.isCollapse)
      } else {
        if (this.isCollapse === false) {
          this.isCollapse = !(this.isCollapse)
        }
      }
    }
  }
}
</script>



