<template>
  <div id="i-search">
    <h2></h2>
    <br/>
    <p v-show="!hasResult"></p>
    <h4 v-show="hasResult"></h4>
    <el-checkbox-group v-model="itemField" v-on:change="change" v-show="hasResult">
      <el-checkbox
        style="width:138px;margin:5px 0;padding:0;"
        v-for="item in field"
        :key="item.label"
        :label="item.label">
      </el-checkbox>
    </el-checkbox-group>
    <br/>
    <div v-show="hasResult">
      <el-table
        border
        :data="searchItemResult"
        style="width: 100%"
        height="800px"
        max-height="1200">
        <!-- ID -->
        <el-table-column
          fixed
          prop="gid"
          label="ID"
          width="120px">
        </el-table-column>
        <!--  -->
        <el-table-column
          fixed
          prop="name"
          label=""
          width="160px">
        </el-table-column>
        <el-table-column
          fixed
          prop="author"
          label=""
          width="160px">
        </el-table-column>
        <el-table-column
          v-if="normNameShow"
          prop="norm_name"
          label=""
          width="160px">
        </el-table-column>
        <el-table-column
          v-if="normAuthorShow"
          prop="norm_author"
          label=""
          width="160px">
        </el-table-column>
        <el-table-column
          v-if="normSeriesShow"
          prop="norm_series"
          label=""
          width="160px">
        </el-table-column>
        <el-table-column
          v-if="rankShow"
          prop="rank"
          label=""
          width="90px">
        </el-table-column>
        <el-table-column
          v-if="tag1Show"
          prop="tag1"
          label="1"
          width="160px">
        </el-table-column>
        <el-table-column
          v-if="tag2Show"
          prop="tag2"
          label="2"
          width="160px">
        </el-table-column>
        <el-table-column
          v-if="viewCountShow"
          prop="view_count"
          label=""
          width="90px">
        </el-table-column>
        <el-table-column
          v-if="statusShow"
          prop="status"
          label="/"
          width="90px">
        </el-table-column>
        <el-table-column
          v-if="feeFlagShow"
          prop="fee_flag"
          label=""
          width="90px">
        </el-table-column>
        <el-table-column
          v-if="ncpShow"
          prop="ncp"
          label="cp"
          width="90px">
        </el-table-column>
        <el-table-column
          v-if="intimeShow"
          prop="intime_stamp"
          label=""
          width="160px">
        </el-table-column>
        <el-table-column
          v-if="uptimeShow"
          prop="chapter_uptime"
          label=""
          width="160px">
        </el-table-column>
        <el-table-column
          v-if="maskLevelShow"
          prop="mask_level"
          label=""
          width="160px">
        </el-table-column>
        <el-table-column
          v-if="byShow"
          prop="by"
          label=""
          width="160px">
        </el-table-column>
        <el-table-column
          v-if="tfShow"
          prop="tf"
          label=""
          width="160px">
        </el-table-column>
        <el-table-column
          v-if="rndShow"
          prop="rn_d"
          label=""
          width="90px">
        </el-table-column>
        <el-table-column
          v-if="rtdShow"
          prop="rt_d"
          label=""
          width="90px">
        </el-table-column>
        <el-table-column
          v-if="rnwShow"
          prop="rn_w"
          label=""
          width="90px">
        </el-table-column>
        <el-table-column
          v-if="rtwShow"
          prop="rt_w"
          label=""
          width="90px">
        </el-table-column>
        <el-table-column
          fixed
          prop="update_time"
          label=""
          width="140px">
        </el-table-column>
      </el-table>
    </div>
    <div style="width:98%; height: 100px;"></div>
  </div>
</template>

<script>
import bus from '@/api/Bus'
export default {
  name: 'MainSearch',
  methods: {
    init () {
      this.normNameShow = false
      this.normAuthorShow = false
      this.normSeriesShow = false
      this.rankShow = false
      this.tag1Show = false
      this.tag2Show = false
      this.viewCountShow = false
      this.statusShow = false
      this.feeFlagShow = false
      this.ncpShow = false
      this.intimeShow = false
      this.uptimeShow = false
      this.maskLevelShow = false
      this.byShow = false
      this.tfShow = false
      this.rndShow = false
      this.rtdShow = false
      this.rnwShow = false
      this.rtwShow = false
    },
    change () {
      this.init()
      for (var i = 0; i < this.itemField.length; i++) {
        switch (this.itemField[i]) {
          case '':
            this.normNameShow = true
            break
          case '':
            this.normAuthorShow = true
            break
          case '':
            this.normSeriesShow = true
            break
          case '':
            this.rankShow = true
            break
          case '1':
            this.tag1Show = true
            break
          case '2':
            this.tag2Show = true
            break
          case '':
            this.viewCountShow = true
            break
          case '/':
            this.statusShow = true
            break
          case '':
            this.feeFlagShow = true
            break
          case 'cp':
            this.ncpShow = true
            break
          case '':
            this.intimeShow = true
            break
          case '':
            this.uptimeShow = true
            break
          case '':
            this.maskLevelShow = true
            break
          case '':
            this.byShow = true
            break
          case '':
            this.tfShow = true
            break
          case '':
            this.rndShow = true
            break
          case '':
            this.rtdShow = true
            break
          case '':
            this.rnwShow = true
            break
          case '':
            this.rtwShow = true
            break
        }
      }
    }
  },
  created () {
    bus.$on('item', (r) => {
      this.searchItemResult = r // .searchItemResult = r
      this.hasResult = r.length > 0
    })
  },
  watch: {
    responseData (val) {
      this.searchItemResult = val
    }
  },
  data: function () {
    return {
      itemField: [
        '',
        '',
        '',
        '',
        '1',
        '2',
        '',
        '/',
        '',
        'cp',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ],
      field: [
        {name: 'norm_name', label: ''},
        {name: 'norm_author', label: ''},
        {name: 'norm_series', label: ''},
        {name: 'rank', label: ''},
        {name: 'tag1', label: '1'},
        {name: 'tag2', label: '2'},
        {name: 'view_count', label: ''},
        {name: 'status', label: '/'},
        {name: 'fee_flag', label: ''},
        {name: 'ncp', label: 'cp'},
        {name: 'intime_stamp', label: ''},
        {name: 'chapter_update', label: ''},
        {name: 'mask_level', label: ''},
        {name: 'by', label: ''},
        {name: 'tf', label: ''},
        {name: 'rn_d', label: ''},
        {name: 'rt_d', label: ''},
        {name: 'rn_w', label: ''},
        {name: 'rt_w', label: ''}
      ],
      hasResult: false,
      responseData: [],
      searchItemResult: [],
      normNameShow: true,
      normAuthorShow: true,
      normSeriesShow: true,
      rankShow: true,
      tag1Show: true,
      tag2Show: true,
      viewCountShow: true,
      statusShow: true,
      feeFlagShow: true,
      ncpShow: true,
      intimeShow: true,
      uptimeShow: true,
      maskLevelShow: true,
      byShow: true,
      tfShow: true,
      rndShow: true,
      rtdShow: true,
      rnwShow: true,
      rtwShow: true
    }
  }
}
</script>

<style>
  #i-search {
    width: 100%;
    padding: 6px 18px;
    background-color: #ffffff;
  }
  #i-search>h2 {
    width: 180px;
    margin: auto;
    font-weight: lighter;
  }
  #i-search>h4 {
    width: 180px;
    margin: 12px 0;
    font-weight: lighter;
  }
</style>



